.yn-cookies{position:fixed;top:0;left:0;width:100%;height:100%;background-color:rgba(0,0,0,0.8);display:none;justify-content:center;align-items:center;z-index:10000}.yn-cookies--show{display:flex}.yn-cookies__inner{width:80%;max-width:800px;background-color:white;max-height:75vh}.yn-cookies__page{padding:32px;max-height:75vh;overflow-y:auto}.yn-cookies__page--visible{display:block}.yn-cookies__page--hidden{display:none}.yn-cookies__body{margin-bottom:32px;border-bottom:1px solid lightgrey;padding-bottom:17px}.yn-cookies__footer ul{padding:0;margin:0;list-style-type:none;display:flex;justify-content:flex-end}.yn-cookies__footer ul li{display:flex;align-items:center}.yn-cookies__footer ul li.link-left{margin-right:auto}.yn-cookies__button{display:block;background-color:#e91e63;padding:20px;color:white}.yn-cookies__switch{position:relative;display:inline-block;width:60px;height:34px}.yn-cookies__settings{padding:0;margin:0;list-style-type:none}.yn-cookies__settings__switch{display:flex;justify-content:flex-end}.yn-cookies__switch input{opacity:0;width:0;height:0}.yn-cookies__slider{position:absolute;cursor:pointer;top:0;left:0;right:0;bottom:0;background-color:#eeeeee;-webkit-transition:0.4s;transition:0.4s}.yn-cookies__slider.disabled{opacity:0.5;pointer-events:none}.yn-cookies__slider:before{position:absolute;content:"";height:26px;width:26px;left:4px;bottom:4px;background-color:white;-webkit-transition:0.4s;transition:0.4s}.yn-cookies__switch input:checked+.yn-cookies__slider{background-color:red}.yn-cookies__switch input:focus+.yn-cookies__slider{box-shadow:0 0 1px red}.yn-cookies__switch input:checked+.yn-cookies__slider:before{-webkit-transform:translateX(26px);-ms-transform:translateX(26px);transform:translateX(26px)}@media only screen and (max-width: 500px){.yn-cookies__inner{width:90%}}@media only screen and (max-width: 440px){.yn-cookies__footer ul{flex-direction:column;justify-content:flex-start}.yn-cookies__footer ul li.link-left{margin-bottom:25px}}.yn-cookie-consent{width:100%;background-color:#eeeeee;min-height:200px;display:flex;justify-content:center;align-items:center;text-align:center}.yn-cookie-consent--inner{width:80%;margin:0 auto}.yn-cookie-consent h5{margin-bottom:20px}.yn-cookie-consent--controlls{margin-top:20px}
